
import { defineComponent, render } from "vue";
import Viewer from "@/components/Viewer.vue"; // @ is an alias to /src
import axios from "axios";

export default defineComponent({
  name: "Home",
  components: {
    Viewer,
  },
  data: function () {
    return {
      fileInfo: "",
      fileName: "no01",
      seen0: true,
      seen1: true,
      seen2: false,
      seen3: false,
      bstr: {},
      file: {},
      previewBase64: {},
    };
  },
  methods: {
    fileSelected(event: any) {
      this.fileInfo = event.target.files[0];
      this.file = event as File;
      this.seen0 = false;
      this.seen2 = true;
      const reader = new FileReader();
      reader.onload = (event:any) => {
                    this.previewBase64 = event.target.result;
                }
                reader.readAsDataURL(event.target.files[0]);
    },
    async fileUpload(): Promise<void> {
      const formData: any = new FormData();
      formData.append("file", this.fileInfo);
      console.log(formData);
      console.log(...formData.entries());
      this.seen0 = false;
      this.seen1 = false;
      this.seen2 = false;
      this.seen3 = true;
      axios.post("https://penta-paint-transformer.herokuapp.com/upload", formData).then((response) => {
        this.bstr = response.data;
        console.log(this.bstr);
        this.seen0 = false;
        this.seen1 = true;
        this.seen3 = false;
      });
    },
  },
});
